import React, { useRef } from "react";

const MalaysiaComponent = ({ getVistaUrl, getIframe }) => {
  const iframe = useRef(null);
  getIframe(iframe) 
  
  return (
    <>
      <iframe
        id="vista"
        ref={iframe}
        style={{ height: "100%", width: "100%", overflow: "hidden", border: "none" }}
        allowFullScreen={false}
        title="Malaysia"
        scrolling="no"
        src={getVistaUrl("malaysia")}
      ></iframe>
    </>
  );
};

const Malaysia = React.memo(MalaysiaComponent);

export { Malaysia };
