import React, { useRef } from "react";

const SingaporeComponent = ({ getVistaUrl, getIframe }) => {
  const iframe = useRef(null);
  getIframe(iframe);
  return (
    <>
      <iframe
        id="vista"
        ref={iframe}
        style={{ height: "100%", width: "100%", overflow: "hidden", border: "none" }}
        allowFullScreen={false}
        title="Singapore"
        scrolling="no"
        src={getVistaUrl("singapore")}
      ></iframe>
    </>
  );
};

const Singapore = React.memo(SingaporeComponent);

export { Singapore };
