import { useEffect, useRef, useState } from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import "./App.css";
import { Footer } from "./components/Footer/Footer";
import { ModalContainer } from "./components/ModalContainer/ModalContainer";
import { Sidebar } from "./components/Sidebar/Sidebar";
import { Malaysia, Singapore, Indonesia, Philippines } from "./pages";
import { routes } from "./constants/routes";
import { goFullScreen, exitFullscreen } from "./utils/fullscreenHelper";
import { useWindowSize } from "./utils/newScreenHeightHelper";

// const getDomain =
//   process.env.NODE_ENV === "development"
//     ? "http://localhost:3000/"
//     : "https://panasonic.com/";

const getVistaUrl = (country) => {
  const urlSwitch = {
    malaysia: "/space-solution/malaysia/index.htm",
    singapore: "/space-solution/singapore/index.htm",
    indonesia: "/space-solution/indonesia/index.htm",
    philippines: "/space-solution/philippines/index.htm",
  };

  return urlSwitch[country];
};

function App() {
  const [windowWidth, windowHeight] = useWindowSize();

  useEffect(() => {
    function callback(e) {
      if (e.data.fullscreen) {
        goFullScreen();
      } else if (e.data.exit) {
        exitFullscreen();
      }
    }
    window.addEventListener("message", callback);

    return () => {
      window.removeEventListener("message", callback);
    };
  }, []);

  const location = useLocation();

  const [vistaContent, setVistaContent] = useState(null)

  const getIframe = (iframe) => {
    setVistaContent(iframe)
    console.log(vistaContent)
    // iframe.current.contentWindow.stop()
    return iframe;
  };

  console.log('app rerender')

  return (
    <div className="App" style={{ height: windowHeight, width: windowWidth }}>
      <Sidebar />
      <ModalContainer vistaContent={vistaContent} />
      <Switch>
        <Route exact path={routes.my} render={() => <Malaysia getVistaUrl={getVistaUrl} getIframe={getIframe} />}></Route>
      </Switch>
      <Switch>
        <Route exact path={routes.sg} render={() => <Singapore getVistaUrl={getVistaUrl} getIframe={getIframe} />}></Route>
      </Switch>
      <Switch>
        <Route exact path={routes.id} render={() => <Indonesia getVistaUrl={getVistaUrl} getIframe={getIframe} />}></Route>
      </Switch>
      <Switch>
        <Route exact path={routes.ph} render={() => <Philippines getVistaUrl={getVistaUrl} getIframe={getIframe} />}></Route>
      </Switch>
      {location.pathname === "/" ? "" : <Footer />}
    </div>
  );
}

export default App;
