import React, { useRef } from "react";

const IndonesiaComponent = ({ getVistaUrl, getIframe }) => {
  const iframe = useRef(null);
  getIframe(iframe);

  return (
    <>
      <iframe
        id="vista"
        ref={iframe}
        style={{ height: "100%", width: "100%", overflow: "hidden", border: "none" }}
        allowFullScreen={false}
        title="Indonesia"
        scrolling="no"
        src={getVistaUrl("indonesia")}
      ></iframe>
    </>
  );
};

const Indonesia = React.memo(IndonesiaComponent);

export { Indonesia };
