import React, { useRef } from "react";

const PhilippinesComponent = ({ getVistaUrl, getIframe }) => {
  const iframe = useRef(null);
  getIframe(iframe);
  
  return (
    <>
      <iframe
        id="vista"
        ref={iframe}
        style={{ height: "100%", width: "100%", overflow: "hidden", border: "none" }}
        allowFullScreen={false}
        title="Philippines"
        scrolling="no"
        src={getVistaUrl("philippines")}
      ></iframe>
    </>
  );
};

const Philippines = React.memo(PhilippinesComponent);

export { Philippines };
